.nav {
  display: none;
  position: fixed;
  top: 66px;
  width: 100%;
  z-index: 2;
}
.nav--hide {
  display: none;
}
.nav--opened {
  display: block;
}
.nav--nojs {
  display: block;
}
@media (min-width: 660px) {
  .nav {
    top: 110px;
  }
}
@media (min-width: 1200px) {
  .nav {
    position: unset;
    z-index: 0;
    display: block;
  }
}

.nav--nojs .nav__wrapper {
  display: block;
  position: static;
  min-height: 0;
}

.nav--nojs .nav__item {
  display: block;
}

@media (min-width: 1200px) {
  .nav__wrapper {
    box-sizing: border-box;
    padding: 30px 130px;
    padding-right: 0;
    margin: 0 auto;
  }
}

.nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (min-width: 1200px) {
  .nav__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 621px;
    padding: 0;
    margin-left: auto;
  }
}

.nav__item {
  background-color: #283645;
  box-shadow: 0 -1px 0 0 #000000, inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: 1200px) {
  .nav__item {
    background-color: transparent;
    box-shadow: none;
  }
  .nav__item--active {
    position: relative;
  }
  .nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 3px;
    left: 0;
    height: 2px;
    background-color: #ffffff;
  }
}

.nav__link {
  display: block;
  padding: 16px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
}
.nav__link:hover {
  color: #d22856;
}
.nav__link :focus {
  outline: 2px solid #ffffff;
  outline-offset: -2px;
}
.nav__link:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1200px) {
  .nav__link {
    padding: 16px 0;
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
  }
  .nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

