.page-footer__wrapper {
  box-sizing: border-box;
  min-width: 320px;
  padding: 40px 20px;
  margin: 0 auto;

  &--form {
    padding-top: 54px;
  }

  @media (min-width: 660px) {
    display: flex;
    justify-content: space-between;
    max-width: 660px;
    padding: 35px 20px;
    padding-bottom: 30px;
  }

  @media (min-width: 1200px) {
    max-width: 1200px;
    padding: 97px 130px;
    padding-bottom: 67px;

    &--index {
      padding-top: 68px;
    }

    &--form {
      padding-top: 71px;
    }
  }
}

.page-footer__link {
  display: block;
  text-decoration: none;
  cursor: pointer;

  &:focus {
    outline: 2px solid #283645;
    border-radius: 1px;
  }
}

.page-footer__logo {
  display: block;
  margin: 0 auto;
  margin-bottom: 39px;
  fill: #d22856;
  cursor: pointer;

  @media (min-width: 660px) {
    margin-bottom: 0;
  }
}

.page-footer__logo-color {
  fill: #283645;
}

.page-footer__logo:hover,
.page-footer__logo-color:hover {
  fill: #d22856;
}

.page-footer__logo:active,
.page-footer__logo-color:active {
  opacity: 0.3;
}

.page-footer__invisible-glasses {
  display: block;

  @media (min-width: 1200px) {
    display: none;
  }
}

.page-footer__invisible-stars {
  display: none;

  @media (min-width: 1200px) {
    display: block;
  }
}

.page-footer__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 29px;
  padding: 0;
  list-style-type: none;

  @media (min-width: 660px) {
    margin: 0;
    padding-right: 15px;
  }

  @media (min-width: 1200px) {
    margin: 0;
    padding-right: 0;
    padding-left: 6px;
  }
}

.page-footer__item {
  margin-right: 6px;
  margin-bottom: 6px;
}

.page-footer__item:nth-child(3n) {
  margin-right: 0;
}

.page-footer__link-social {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 3px solid rgba(40, 54, 69, 0.1);
  cursor: pointer;

  &:hover {
    border: 3px solid #283645;
  }

  &:active {
    border: 3px solid rgba(40, 54, 69, 0.1);
  }
}

.page-footer__icon {
  fill: #d22856;

  &:active {
    fill: rgba(210, 40, 86, 0.3);
  }
}

.page-footer__logo-academy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 130px;

  @media (min-width: 660px) {
    margin: 0;
  }

  @media (min-width: 1200px) {
    width: 153px;
  }
}

.page-footer__desc {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #283645;

  @media (min-width: 660px) {
    padding-bottom: 6px;
  }

  @media (min-width: 1200px) {
    font-size: 18px;
  }
}

.page-footer__logo-academy-icon {
  fill: #d22856;

  &:hover {
    fill: #283645;
  }

  &:focus {
    outline: 2px solid #283645;
    outline-offset: 3px;
    border-radius: 1px;
  }

  &:active {
    fill: rgba(40, 54, 69, 0.3);
  }
}
