.download {
  margin: 0 auto;
  width: 100%;
  z-index: 1;
  background-color: #1d2631;

  @media (min-width: 660px) {
    position: unset;
    top: 377px;
    left: 20px;
    max-width: 300px;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: transparent;
  }

  @media (min-width: 1200px) {
    top: 449px;
    left: 130px;
  }
}

.download__wrapper {
  min-width: 280px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 29px 20px;

  @media (min-width: 660px) {
    min-width: 300px;
    padding: 0;
    padding-bottom: 20px;
    margin: 0;
  }
}

.download__button-link {
  display: block;
  box-sizing: border-box;
  width: 280px;
  padding: 15px;
  margin: 0 auto;
  margin-bottom: 32px;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: transparent;
  border: 3px solid #ffffff;
  border-radius: 29px;

  &:hover {
    color: #d22856;
    background-color: #ffffff;
  }

  &:focus {
    outline: 3px solid #ffffff;
    outline-offset: -3px;
  }

  &:active {
    color: rgba(210, 40, 86, 0.3);
    background-color: #ffffff;
  }

  @media (min-width: 660px) {
    width: 300px;
    margin-bottom: 49px;
  }

  @media (min-width: 1200px) {
    margin-bottom: 67px;
    font-size: 21px;
  }
}

.download__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  margin-bottom: 8px;
  padding: 0;
  list-style-type: none;

  @media (min-width: 660px) {
    margin-bottom: 15px;
  }

  @media (min-width: 1200px) {
    margin-bottom: 39px;
  }
}

.download__item {
  display: flex;
  flex-wrap: wrap;
  align-items: end;
  justify-content: center;
  margin-right: 35px;
  margin-bottom: 10px;
  cursor: pointer;

  @media (min-width: 660px) {
    align-items: center;
  }

  @media (min-width: 1200px) {
    align-items: flex-end;
  }
}

.download__item:nth-child(3n) {
  margin-right: 0;
}

.download__link {
  &:focus {
    outline: 3px solid #ffffff;
    border-radius: 1px;
  }
}

.download__desc {
  display: block;
  margin: 0 auto;
  width: 280px;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;

  @media (min-width: 660px) {
    width: 300px;
    font-size: 18px;
  }
}
